import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const SigmoidFunction = () => {
  const [data, setData] = useState([]);
  
  useEffect(() => {
    const generateData = () => {
      const dataPoints = [];
      for (let x = -10; x <= 10; x += 0.5) {
        const sigmoid = 1 / (1 + Math.exp(-x));
        dataPoints.push({ x, y: sigmoid });
      }
      return dataPoints;
    };
    
    setData(generateData());
  }, []);
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4">: f(x) = 1/(1+e^(-x))</h3>
      <div className="w-full h-64 bg-white p-4 rounded-lg shadow-md">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={data} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="x" 
              label={{ value: 'x', position: 'insideBottomRight', offset: -5 }} 
              domain={[-10, 10]}
            />
            <YAxis 
              label={{ value: 'f(x)', angle: -90, position: 'insideLeft' }} 
              domain={[0, 1.1]}
            />
            <Tooltip 
              formatter={(value) => [value.toFixed(4), 'f(x)']}
              labelFormatter={(value) => `x: ${value}`}
            />
            <Line 
              type="monotone" 
              dataKey="y" 
              stroke="#8884d8" 
              strokeWidth={2} 
              dot={false} 
              name=""
            />
            {/*  */}
            <Line 
              dataKey="y" 
              stroke="transparent" 
              dot={(props) => {
                const { cx, cy, payload } = props;
                if (payload.x === 0) {
                  return (
                    <circle 
                      cx={cx} 
                      cy={cy} 
                      r={4} 
                      stroke="red" 
                      strokeWidth={2} 
                      fill="red" 
                    />
                  );
                }
                return null;
              }}
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
      <div className="mt-4 p-4 bg-blue-50 rounded-lg">
        <h4 className="font-bold mb-2"></h4>
        <ul className="list-disc pl-5">
          <li>x=0f(x)=0.5 ()</li>
          <li>x1x0</li>
          <li>x0  </li>
          <li>: 01</li>
          <li></li>
        </ul>
      </div>
    </div>
  );
};

export default SigmoidFunction;