import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const ActivationFunctionsComparison = () => {
  const [data, setData] = useState([]);
  const alpha = 0.01; // Leaky ReLU
  
  useEffect(() => {
    const generateData = () => {
      const dataPoints = [];
      for (let x = -10; x <= 10; x += 0.5) {
        const sigmoid = 1 / (1 + Math.exp(-x));
        const tanh = Math.tanh(x);
        const relu = Math.max(0, x);
        const leakyRelu = x > 0 ? x : alpha * x;
        dataPoints.push({ 
          x, 
          sigmoid, 
          tanh, 
          relu, 
          leakyRelu 
        });
      }
      return dataPoints;
    };
    
    setData(generateData());
  }, [alpha]);
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4"></h3>
      <div className="w-full h-80 bg-white p-4 rounded-lg shadow-md">
        <ResponsiveContainer width="100%" height="100%">
          <LineChart data={data} margin={{ top: 5, right: 30, left: 20, bottom: 5 }}>
            <CartesianGrid strokeDasharray="3 3" />
            <XAxis 
              dataKey="x" 
              label={{ value: 'x', position: 'insideBottomRight', offset: -5 }} 
              domain={[-6, 6]}
            />
            <YAxis 
              label={{ value: 'f(x)', angle: -90, position: 'insideLeft' }} 
              domain={[-1.5, 6]}
            />
            <Tooltip 
              formatter={(value) => [value.toFixed(4), '']}
              labelFormatter={(value) => `x: ${value}`}
            />
            <Legend />
            <Line 
              type="monotone" 
              dataKey="sigmoid" 
              stroke="#8884d8" 
              strokeWidth={2} 
              dot={false} 
              name=""
            />
            <Line 
              type="monotone" 
              dataKey="tanh" 
              stroke="#82ca9d" 
              strokeWidth={2} 
              dot={false} 
              name="tanh"
            />
            <Line 
              type="monotone" 
              dataKey="relu" 
              stroke="#ff7300" 
              strokeWidth={2} 
              dot={false} 
              name="ReLU"
            />
            <Line 
              type="monotone" 
              dataKey="leakyRelu" 
              stroke="#8844d8" 
              strokeWidth={2} 
              dot={false} 
              name="Leaky ReLU"
            />
          </LineChart>
        </ResponsiveContainer>
      </div>
      
      <div className="mt-6 w-full p-4 bg-gray-50 rounded-lg">
        <h4 className="font-bold mb-3">G</h4>
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="p-2 border font-bold"></td>
                <td className="p-2 border">01</td>
                <td className="p-2 border"></td>
                <td className="p-2 border"></td>
                <td className="p-2 border"><br/></td>
                <td className="p-2 border"></td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">tanh</td>
                <td className="p-2 border">-11</td>
                <td className="p-2 border"></td>
                <td className="p-2 border"><br/></td>
                <td className="p-2 border"></td>
                <td className="p-2 border">RNN<br/></td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">ReLU</td>
                <td className="p-2 border">0</td>
                <td className="p-2 border">x=0</td>
                <td className="p-2 border"><br/><br/></td>
                <td className="p-2 border">Dying ReLU<br/>()</td>
                <td className="p-2 border">CNN<br/></td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">Leaky ReLU</td>
                <td className="p-2 border">-</td>
                <td className="p-2 border">x=0</td>
                <td className="p-2 border">Dying ReLU<br/>ReLU</td>
                <td className="p-2 border"><br/>ReLU</td>
                <td className="p-2 border">ReLU</td>
              </tr>
              <tr>
                <td className="p-2 border font-bold">Softmax</td>
                <td className="p-2 border">01<br/>(=1)</td>
                <td className="p-2 border"></td>
                <td className="p-2 border"><br/>1</td>
                <td className="p-2 border"><br/></td>
                <td className="p-2 border"></td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      
      <div className="mt-4 p-4 bg-yellow-50 rounded-lg w-full">
        <h4 className="font-bold mb-2">G</h4>
        <ul className="list-disc pl-5">
          <li>0</li>
          <li>ReLU(ReLU, Leaky ReLU)</li>
          <li>Softmax</li>
          <li>ReLU</li>
          <li></li>
        </ul>
      </div>
    </div>
  );
};

export default ActivationFunctionsComparison;