import React from 'react';

const MetricsComparison = () => {
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4"></h3>
      
      <div className="w-full p-4 bg-white rounded-lg shadow-md mb-6">
        <p className="mb-4"></p>
        
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white">
            <thead>
              <tr>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
                <th className="p-2 border"></th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td className="p-2 border"><br/>()</td>
                <td className="p-2 border font-bold text-purple-700">Recall ()</td>
                <td className="p-2 border"></td>
              </tr>
              <tr>
                <td className="p-2 border"></td>
                <td className="p-2 border font-bold text-green-700">Precision ()</td>
                <td className="p-2 border"></td>
              </tr>
              <tr>
                <td className="p-2 border"></td>
                <td className="p-2 border font-bold text-red-700">F1 Score</td>
                <td className="p-2 border">RecallPrecision</td>
              </tr>
              <tr>
                <td className="p-2 border"><br/></td>
                <td className="p-2 border font-bold text-blue-700">Accuracy ()</td>
                <td className="p-2 border"></td>
              </tr>
              <tr>
                <td className="p-2 border"><br/>(: )</td>
                <td className="p-2 border font-bold text-red-700">F1 Score</td>
                <td className="p-2 border">PrecisionRecall</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      
      {/*  */}
      <div className="w-full bg-gray-50 p-4 rounded-lg mb-6">
        <h4 className="font-bold mb-3"></h4>
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-white p-3 rounded-lg shadow">
            <h5 className="font-bold text-green-700 mb-2">Precision </h5>
            <div className="flex justify-center mb-3">
              <svg width="180" height="120" viewBox="0 0 180 120">
                <circle cx="60" cy="60" r="40" fill="#EFF6FF" stroke="#3B82F6" strokeWidth="2" />
                <circle cx="120" cy="60" r="40" fill="#FEF2F2" stroke="#EF4444" strokeWidth="2" />
                <circle cx="75" cy="60" r="25" fill="#93C5FD" opacity="0.8" />
                <text x="45" y="100" fontSize="12" textAnchor="middle"></text>
                <text x="135" y="100" fontSize="12" textAnchor="middle"></text>
                <text x="75" y="20" fontSize="12" textAnchor="middle">:</text>
              </svg>
            </div>
            <p className="text-sm">
              <strong>:</strong> 
            </p>
            <p className="text-sm mt-1">
              <strong>:</strong> 
            </p>
          </div>
          
          <div className="bg-white p-3 rounded-lg shadow">
            <h5 className="font-bold text-purple-700 mb-2">Recall </h5>
            <div className="flex justify-center mb-3">
              <svg width="180" height="120" viewBox="0 0 180 120">
                <circle cx="60" cy="60" r="40" fill="#EFF6FF" stroke="#3B82F6" strokeWidth="2" />
                <circle cx="120" cy="60" r="40" fill="#FEF2F2" stroke="#EF4444" strokeWidth="2" />
                <ellipse cx="60" cy="60" rx="35" ry="30" fill="#93C5FD" opacity="0.8" />
                <text x="45" y="100" fontSize="12" textAnchor="middle"></text>
                <text x="135" y="100" fontSize="12" textAnchor="middle"></text>
                <text x="75" y="20" fontSize="12" textAnchor="middle">:</text>
              </svg>
            </div>
            <p className="text-sm">
              <strong>:</strong> 
            </p>
            <p className="text-sm mt-1">
              <strong>:</strong> 
            </p>
          </div>
        </div>
      </div>
      
      {/* Precision-Recall */}
      <div className="w-full bg-white p-4 rounded-lg shadow-md">
        <h4 className="font-bold mb-3 text-center">Precision-Recall</h4>
        
        <div className="flex justify-center mb-4">
          <svg width="400" height="220" viewBox="0 0 400 220">
            {/*  */}
            <line x1="50" y1="170" x2="350" y2="170" stroke="black" strokeWidth="2" />
            <line x1="50" y1="170" x2="50" y2="20" stroke="black" strokeWidth="2" />
            
            {/*  */}
            <text x="380" y="175" fontSize="12"></text>
            <text x="30" y="10" fontSize="12"></text>
            
            {/*  */}
            <path d="M50,170 C100,150 130,100 170,80 C210,60 240,50 270,45 C300,40 320,38 350,35" 
                  stroke="#047857" strokeWidth="2" fill="none" />
            <path d="M50,35 C80,40 110,45 140,60 C170,75 200,110 230,130 C260,150 300,160 350,170" 
                  stroke="#7E22CE" strokeWidth="2" fill="none" />
            
            {/*  */}
            <text x="320" y="40" fontSize="12" fill="#047857" fontWeight="bold">Precision</text>
            <text x="320" y="150" fontSize="12" fill="#7E22CE" fontWeight="bold">Recall</text>
            
            {/*  */}
            <line x1="200" y1="185" x2="200" y2="170" stroke="red" strokeWidth="2" />
            <text x="200" y="200" fontSize="10" textAnchor="middle"></text>
            
            {/* F1 */}
            <circle cx="200" cy="95" r="5" fill="red" />
            <text x="200" y="85" fontSize="10" textAnchor="middle">F1</text>
          </svg>
        </div>
        
        <div className="text-sm p-3 bg-yellow-50 rounded">
          <p><strong>PrecisionRecall:</strong></p>
          <ul className="list-disc pl-5 mt-2">
            <li>: PrecisionRecall</li>
            <li>: RecallPrecision</li>
            <li>F1PrecisionRecall</li>
            <li><strong>G:</strong> </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default MetricsComparison;