import React from 'react';

const ConfusionMatrix = () => {
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4"> (Confusion Matrix)</h3>
      
      <div className="w-full p-4 bg-gray-50 rounded-lg mb-6">
        <p className="mb-4">2/4</p>
        <ul className="list-disc pl-5 mb-4">
          <li><strong>TP: True Positive</strong>: </li>
          <li><strong>FP: False Positive</strong>: </li>
          <li><strong>TN: True Negative</strong>: </li>
          <li><strong>FN: False Negative</strong>: </li>
        </ul>
      </div>
      
      <div className="relative w-full max-w-xl mb-6">
        {/*  */}
        <div className="absolute -left-32 top-1/2 transform -translate-y-1/2 -rotate-90 text-lg font-bold">
          
        </div>
        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-8 text-lg font-bold">
          
        </div>
        
        {/*  */}
        <div className="grid grid-cols-2 gap-1">
          {/*  */}
          <div className="flex justify-end pr-2">
            <div className="w-16 h-16 flex items-center justify-center font-bold bg-gray-200 rounded-tl-lg">
            </div>
          </div>
          <div className="flex">
            <div className="w-1/2 flex items-center justify-center font-bold bg-gray-200">
              
            </div>
            <div className="w-1/2 flex items-center justify-center font-bold bg-gray-200 rounded-tr-lg">
              
            </div>
          </div>
          
          {/* 1:  */}
          <div className="flex justify-end pr-2 items-center">
            <div className="h-32 flex items-center justify-center font-bold bg-gray-200">
              
            </div>
          </div>
          <div className="flex h-32">
            <div className="w-1/2 flex flex-col items-center justify-center p-2 bg-green-100 border-2 border-green-500">
              <div className="text-2xl font-bold text-green-700">TP</div>
              <div className="text-sm mt-1"></div>
              <div className="text-xs mt-1">(True Positive)</div>
            </div>
            <div className="w-1/2 flex flex-col items-center justify-center p-2 bg-red-100 border-2 border-red-500">
              <div className="text-2xl font-bold text-red-700">FN</div>
              <div className="text-sm mt-1"></div>
              <div className="text-xs mt-1">(False Negative)</div>
            </div>
          </div>
          
          {/* 2:  */}
          <div className="flex justify-end pr-2 items-center">
            <div className="h-32 flex items-center justify-center font-bold bg-gray-200 rounded-bl-lg">
              
            </div>
          </div>
          <div className="flex h-32">
            <div className="w-1/2 flex flex-col items-center justify-center p-2 bg-red-100 border-2 border-red-500">
              <div className="text-2xl font-bold text-red-700">FP</div>
              <div className="text-sm mt-1"></div>
              <div className="text-xs mt-1">(False Positive)</div>
            </div>
            <div className="w-1/2 flex flex-col items-center justify-center p-2 bg-green-100 border-2 border-green-500 rounded-br-lg">
              <div className="text-2xl font-bold text-green-700">TN</div>
              <div className="text-sm mt-1"></div>
              <div className="text-xs mt-1">(True Negative)</div>
            </div>
          </div>
        </div>
      </div>
      
      {/*  */}
      <div className="w-full p-4 bg-blue-50 rounded-lg">
        <h4 className="font-bold mb-2">: </h4>
        <ul className="list-disc pl-5">
          <li><strong>TP</strong>: </li>
          <li><strong>FP</strong>: </li>
          <li><strong>TN</strong>: </li>
          <li><strong>FN</strong>: </li>
        </ul>
      </div>
    </div>
  );
};

export default ConfusionMatrix;