import React, { useState } from 'react';

const MetricsVisualizer = () => {
  // 
  const initialValues = {
    tp: 80,
    fp: 20,
    fn: 10,
    tn: 90
  };
  
  const [values, setValues] = useState(initialValues);
  
  // 
  const calculateMetrics = () => {
    const { tp, fp, fn, tn } = values;
    
    // 
    const total = tp + fp + fn + tn;
    
    // Accuracy
    const accuracy = (tp + tn) / total;
    
    // Precision
    const precision = tp / (tp + fp);
    
    // Recall
    const recall = tp / (tp + fn);
    
    // F1 Score
    const f1 = 2 * (precision * recall) / (precision + recall);
    
    return {
      accuracy,
      precision,
      recall,
      f1
    };
  };
  
  const metrics = calculateMetrics();
  
  // 
  const handleChange = (metric, value) => {
    const numValue = parseInt(value, 10);
    if (!isNaN(numValue) && numValue >= 0) {
      setValues({
        ...values,
        [metric]: numValue
      });
    }
  };
  
  // 
  const resetValues = () => {
    setValues(initialValues);
  };
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4"></h3>
      
      {/*  */}
      <div className="w-full p-4 bg-gray-50 rounded-lg mb-6">
        <p className="mb-2"></p>
        <p></p>
      </div>
      
      {/*  */}
      <div className="w-full max-w-2xl bg-white p-4 rounded-lg shadow-md mb-6">
        <h4 className="font-bold mb-4 text-center"></h4>
        
        <div className="grid grid-cols-2 gap-4 mb-4">
          <div className="flex flex-col items-center p-3 bg-green-100 rounded-lg border border-green-400">
            <label className="font-bold mb-1">TP ()</label>
            <input
              type="number"
              min="0"
              value={values.tp}
              onChange={(e) => handleChange('tp', e.target.value)}
              className="w-full p-2 border rounded text-center"
            />
          </div>
          
          <div className="flex flex-col items-center p-3 bg-red-100 rounded-lg border border-red-400">
            <label className="font-bold mb-1">FP ()</label>
            <input
              type="number"
              min="0"
              value={values.fp}
              onChange={(e) => handleChange('fp', e.target.value)}
              className="w-full p-2 border rounded text-center"
            />
          </div>
          
          <div className="flex flex-col items-center p-3 bg-red-100 rounded-lg border border-red-400">
            <label className="font-bold mb-1">FN ()</label>
            <input
              type="number"
              min="0"
              value={values.fn}
              onChange={(e) => handleChange('fn', e.target.value)}
              className="w-full p-2 border rounded text-center"
            />
          </div>
          
          <div className="flex flex-col items-center p-3 bg-green-100 rounded-lg border border-green-400">
            <label className="font-bold mb-1">TN ()</label>
            <input
              type="number"
              min="0"
              value={values.tn}
              onChange={(e) => handleChange('tn', e.target.value)}
              className="w-full p-2 border rounded text-center"
            />
          </div>
        </div>
        
        <div className="flex justify-center">
          <button
            onClick={resetValues}
            className="px-4 py-2 bg-blue-500 text-white rounded hover:bg-blue-600"
          >
            
          </button>
        </div>
      </div>
      
      {/*  */}
      <div className="w-full grid grid-cols-1 md:grid-cols-2 gap-4">
        {/* Accuracy */}
        <div className="bg-white p-4 rounded-lg shadow-md">
          <h4 className="font-bold mb-2 text-center text-blue-700">Accuracy ()</h4>
          <div className="h-6 w-full bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-blue-500 rounded-full"
              style={{ width: `${metrics.accuracy * 100}%` }}
            ></div>
          </div>
          <div className="mt-2 text-center font-bold text-lg">
            {(metrics.accuracy * 100).toFixed(1)}%
          </div>
          <div className="mt-2 text-sm">
            <p className="font-bold">: (TP + TN) / (TP + FP + FN + TN)</p>
            <p className="mt-1"></p>
            <p className="mt-2 bg-yellow-50 p-2 rounded">
              <strong>:</strong> Accuracy
            </p>
          </div>
        </div>
        
        {/* Precision */}
        <div className="bg-white p-4 rounded-lg shadow-md">
          <h4 className="font-bold mb-2 text-center text-green-700">Precision ()</h4>
          <div className="h-6 w-full bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-green-500 rounded-full"
              style={{ width: `${metrics.precision * 100}%` }}
            ></div>
          </div>
          <div className="mt-2 text-center font-bold text-lg">
            {(metrics.precision * 100).toFixed(1)}%
          </div>
          <div className="mt-2 text-sm">
            <p className="font-bold">: TP / (TP + FP)</p>
            <p className="mt-1"></p>
            <p className="mt-2 bg-yellow-50 p-2 rounded">
              <strong>:</strong> 
            </p>
          </div>
        </div>
        
        {/* Recall */}
        <div className="bg-white p-4 rounded-lg shadow-md">
          <h4 className="font-bold mb-2 text-center text-purple-700">Recall ()</h4>
          <div className="h-6 w-full bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-purple-500 rounded-full"
              style={{ width: `${metrics.recall * 100}%` }}
            ></div>
          </div>
          <div className="mt-2 text-center font-bold text-lg">
            {(metrics.recall * 100).toFixed(1)}%
          </div>
          <div className="mt-2 text-sm">
            <p className="font-bold">: TP / (TP + FN)</p>
            <p className="mt-1"></p>
            <p className="mt-2 bg-yellow-50 p-2 rounded">
              <strong>:</strong> 
            </p>
          </div>
        </div>
        
        {/* F1 Score */}
        <div className="bg-white p-4 rounded-lg shadow-md">
          <h4 className="font-bold mb-2 text-center text-red-700">F1 Score</h4>
          <div className="h-6 w-full bg-gray-200 rounded-full overflow-hidden">
            <div
              className="h-full bg-red-500 rounded-full"
              style={{ width: `${metrics.f1 * 100}%` }}
            ></div>
          </div>
          <div className="mt-2 text-center font-bold text-lg">
            {(metrics.f1 * 100).toFixed(1)}%
          </div>
          <div className="mt-2 text-sm">
            <p className="font-bold">: 2 * (Precision * Recall) / (Precision + Recall)</p>
            <p className="mt-1">PrecisionRecall</p>
            <p className="mt-2 bg-yellow-50 p-2 rounded">
              <strong>:</strong> PrecisionRecall
            </p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default MetricsVisualizer;