import React, { useState } from 'react';

const SoftmaxVisualizer = () => {
  const [inputValues, setInputValues] = useState([2, 1, 0, 3]);
  const [customInputs, setCustomInputs] = useState(inputValues.join(', '));
  
  // Softmax
  const calculateSoftmax = (values) => {
    // e^xi 
    const expValues = values.map(val => Math.exp(val));
    // 
    const sumExpValues = expValues.reduce((a, b) => a + b, 0);
    // softmax
    return expValues.map(expVal => expVal / sumExpValues);
  };
  
  const softmaxValues = calculateSoftmax(inputValues);
  
  // 
  const handleCustomInputChange = (e) => {
    setCustomInputs(e.target.value);
  };
  
  const handleApplyCustom = () => {
    try {
      const newValues = customInputs.split(',').map(val => parseFloat(val.trim()));
      if (newValues.some(isNaN)) {
        alert('');
        return;
      }
      setInputValues(newValues);
    } catch (error) {
      alert(': 2, 1, 0, 3');
    }
  };
  
  return (
    <div className="flex flex-col items-center w-full visualization-section">
      <h3 className="text-xl font-bold mb-4">Softmax</h3>
      
      <div className="w-full bg-white p-4 rounded-lg shadow-md mb-4">
        <div className="flex flex-col mb-4">
          <div className="text-center p-2 bg-yellow-50 rounded mb-4">
            <p className="font-bold">Softmax: </p>
            <p className="mt-1">softmax(x<sub>i</sub>) = e<sup>x<sub>i</sub></sup> /  e<sup>x<sub>j</sub></sup></p>
          </div>
          
          <label className="block text-sm font-medium mb-1"> ():</label>
          <div className="flex">
            <input
              type="text"
              value={customInputs}
              onChange={handleCustomInputChange}
              className="border rounded p-2 flex-grow"
              placeholder=": 2, 1, 0, 3"
            />
            <button
              onClick={handleApplyCustom}
              className="ml-2 bg-blue-500 text-white px-4 py-2 rounded"
            >
              
            </button>
          </div>
        </div>
        
        <div className="flex flex-col">
          <div className="mb-4">
            <h4 className="font-bold"> (x)</h4>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-2 mt-2">
              {inputValues.map((val, index) => (
                <div key={`input-${index}`} className="bg-gray-100 p-2 rounded text-center">
                  x<sub>{index}</sub> = {val.toFixed(2)}
                </div>
              ))}
            </div>
          </div>
          
          <div className="relative h-12 flex items-center justify-center bg-gray-50 rounded">
            <div className="absolute left-0 transform -translate-y-1/2 rotate-90 origin-left text-gray-500 ml-2">
              Softmax
            </div>
            <div className="w-8 h-8 flex items-center justify-center">
              <svg viewBox="0 0 24 24" width="24" height="24">
                <path d="M7 10l5 5 5-5z" fill="currentColor" />
              </svg>
            </div>
          </div>
          
          <div className="mt-4">
            <h4 className="font-bold"> (Softmax(x))</h4>
            <div className="grid grid-cols-1 md:grid-cols-4 gap-2 mt-2">
              {softmaxValues.map((val, index) => (
                <div 
                  key={`output-${index}`} 
                  className="relative bg-blue-100 p-2 rounded text-center"
                  style={{
                    background: `linear-gradient(90deg, rgba(59, 130, 246, ${val.toFixed(2)}) 0%, rgba(59, 130, 246, 0.1) 100%)`
                  }}
                >
                  <div>softmax(x<sub>{index}</sub>) = {val.toFixed(4)}</div>
                  <div className="text-xs mt-1">{(val * 100).toFixed(1)}%</div>
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>
      
      <div className="w-full p-4 bg-blue-50 rounded-lg">
        <h4 className="font-bold mb-2">Softmax</h4>
        <ul className="list-disc pl-5">
          <li>1 ()</li>
          <li> ()</li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
      </div>
    </div>
  );
};

export default SoftmaxVisualizer;